import {Component, ElementRef, EventEmitter, Input, Output, ViewEncapsulation} from '@angular/core';
import {AdaptAgreementState} from '../argeement.model';
import {
  AdaptRadarAngularCustomEventName,
  AdaptRadarAngularGenericDirective,
  AdaptRadarAngularService
} from '../../common/radar/adapt-radar';

@Component({
  selector: 'adapt-agreement-admin-content',
  styleUrls: ['../agreement.scss'],
  templateUrl: './agreement-admin-content.component.html',
  encapsulation: ViewEncapsulation.None
})
export class AdaptAgreementAdminContentComponent extends AdaptRadarAngularGenericDirective {
  isCollapsedPerformanceContent: boolean = true;
  isCollapsedMarketingContent: boolean = true;

  @Input() multiProductUsage: boolean = false;

  @Input() state: AdaptAgreementState;

  @Input() showOrganizationSettings: boolean;

  @Output() readonly stateChange: EventEmitter<AdaptAgreementState> = new EventEmitter<AdaptAgreementState>();

  constructor(_adaptRadarService: AdaptRadarAngularService,
              _elem: ElementRef<HTMLElement>) {
    super(_adaptRadarService, _elem, AdaptRadarAngularCustomEventName.AdaptAgreementAdminContent);
  }

  handleStateChange(): void {
    if (this.showOrganizationSettings) {
      if (!this.state.organizationPerformance) {
        this.state.organizationMarketing = false;
        this.state.accountPerformance = false;
      }

      if (!this.state.organizationMarketing) {
        this.state.accountMarketing = false;
      }
    } else {
      if (!this.state.accountPerformance) {
        this.state.accountMarketing = false;
      }
    }

    this.stateChange.emit(this.state);
  }
}
